



export const todoReducer = (state = [], action) => {
    switch (action.type) {
        case 'add':
            return [ ...state, action.payload ];

        case 'delete':
            return state.filter( todo => todo.id !== action.payload);

        case 'toggle':
            return state.map( e => 
                (e.id === action.payload)
                ? { ...e , done: !e.done}
                : e
            )
        default:
            return state;
    }
};
